.page {
  min-height: 100vh;
  position: relative;
}

/* bg2 */

.inner {
  position: relative;
  padding-top: 70px;
  padding-bottom: 70px;
}
.bg {
  position: absolute;
  top: 70px;
  left: 0;
  width: 100%;
  height: 100%;
}
.gradient1,
.gradient2 {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.gradient1 {
  background-image: linear-gradient(
    179.98deg,
    rgba(255, 255, 255, 0.97) 0.01%,
    rgba(255, 255, 255, 0) 38.1%,
    #ffffff 91.05%,
    #ffffff 99.99%
  );
}
.gradient2 {
  background-image: linear-gradient(
    88.8deg,
    #ffffff 22.24%,
    rgba(255, 255, 255, 0.33) 98.56%
  );
}
.bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

