.text {
  max-width: 70%;
  font-size: 15px;
  line-height: 1.5;
  letter-spacing: 0.05em;
  color: var(--blue);
  margin-bottom: 35px;
}
.text:last-child{
    margin-bottom: 0;
}
.text span{
    font-weight: 600;
}

.download{
    position: relative;
}

.download::after{
    content: "";
    position: absolute;
    bottom: -2px;
    left: 50%;
    transform: translateX(-50%);
    height: 1px;
    background-color: var(--blue);
    width: 0%;
    transition: .4s ease .2s;
}

.download:hover::after{
    width: 80%;
}