.header {
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;

  padding: 25px 0;

  background: rgba(104, 146, 203, 0.17);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}
.header.footer {
  position: relative;
  background: rgba(80, 147, 200, 0.73);
}
