.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
}
.logo_img {
  margin-right: 10px;
}
.logo_title {
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  letter-spacing: -0.045em;
  margin-right: 10px;
}

.logo_subtitle {
  font-style: normal;
  font-size: 15px;
  font-weight: 300;
  letter-spacing: 0.11em;
}
.logo_subtitle p:first-child {
  margin-bottom: 3px;
}

/* nav */
.nav_inner {
  display: flex;
}
.link {
  display: inline-block;
  cursor: pointer;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: 0.045em;
  margin-right: 25px;
  transition: 0.6s ease;
  position: relative;
}
.link::after {
  content: "";
  position: absolute;
  bottom: -4px;
  height: 1px;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  width: 0;
  background-color: var(--blue);

  transition: 0.4s ease 0.2s;
}
.link:hover::after{
  width: 80%;
  opacity: 1;
}
.link:hover, .link.active{
  color: var(--blue);
}

.link:last-child {
  margin-right: 0;
}

/* langs */
.languages {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.language:not(.small) {
  margin-right: 10px;
}
.language.small img {
  width: 16px;
  height: 16px;
}
.extra {
  position: absolute;
  left: 2px;
  bottom: -25px;
  opacity: 0;
  transform: scale(0.2);
  transition: 0.4s ease;
}
.extra.active {
  transform: scale(1);
  opacity: 1;
}
.arrow {
  transition: 0.4s ease;
}
.arrow.active {
  transform: rotate(-180deg);
}


/* links extra */
.seamans{
  position: relative;
  margin-right: 25px;
}
.seamans .link{
  margin-right: 0;
}
.links_extra{
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 25px;
  left: -15px;
  padding: 15px;
  min-width: 180px;
  border-radius:  10px;

  background: rgba(104, 146, 203, 0.17);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);

  opacity: 0;
  visibility: hidden;
  transition: .4s ease;
  transform: scale(.5);
}
.header.header.footer .links_extra{
  top: -120px;
  background: rgb(22 95 171 / 57%);
}
.links_extra.active{
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}
.links_extra .link{
  margin-bottom: 10px;
}
.links_extra .link:last-child{
  margin-bottom: 0px;
}
.links_extra .link:hover::after{
  opacity: 0;
}